(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
    .service('stopableInterval', stopableInterval);

  /** @ngInject */
  function stopableInterval($window) {
    return {
      start: function (interval, calback, time) {
        function startInterval() {
          return interval(calback, time);
        }

        var i = startInterval();

        angular.element($window).bind('focus', function () {
          if (i) interval.cancel(i);
          i = startInterval();
        });

        angular.element($window).bind('blur', function () {
          if (i) interval.cancel(i);
        });
      }
    }
  }

})();
(function () {
    'use strict';

    angular.module('BlurAdmin.pages.auth')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($scope, $rootScope, $window, $state, AuthService, NotificationsService) {
        var vm = this;

        activate();
        function activate() {
            vm.account = {}
            vm.loading = false
            $rootScope.user = null
            $rootScope.utils_template = $state.current.templateUrl
            $rootScope.utils_app = true
            $window.localStorage.removeItem('token')
        }
        function deactivate() {
            $rootScope.utils_template = null
            $rootScope.utils_app = null
        }

        vm.logout = function() {
            $window.localStorage.removeItem('token');
            $state.go('login')
        }

        var login_validate = function() {
            if (!vm.account.username || !vm.account.password) {
                var title = "Campos vacos o incorrectos"
                var msg = "Por favor, llena los campos correctamente."
                NotificationsService.default_toast(title, msg, NotificationsService.ERROR)
                return false
            }
            return true
        }
        function onError(error) {
            var title = "Error en la autenticacin"
            var msg = "Usuario y/o contrasea invlidos"
            NotificationsService.default_toast(title, msg, NotificationsService.ERROR)
            vm.loading = false
            vm.logout()
            return false
        }
        vm.login = function() {
            //  DESACTIVA EL BOTN MIENTRAS EST CARGANDO LA AUTENTICACIN
            //  PARA EVITAR QUE EL USUARIO MANDE MS DE UNA PETICIN
            //  MIENTRAS SE EST PROCESANDO UNA PETICIN EN BACKEND
            vm.loading = true

            if (login_validate()) {
                return AuthService
                    .login({ username: (vm.account.username || ""), password: (vm.account.password || "") })
                    .then(function(data) {
                        //  IR A DASHBOARD Y GUARDAR EL TOKEN EN LA SESSION STORAGE
                        deactivate()
                        $window.localStorage.setItem('token', data.token);
                        //return $state.go('dashboard')
                        var title = "Bienvenido/a"
                        //var msg = "Usuario y/o contrasea invlidos"
                        NotificationsService.default_toast(title, '', NotificationsService.SUCCESS)

                        return AuthService
                            .me()
                            .then(function(response) {
                                if (!response.eliminado)
                                    return $state.go('citas')
                                else
                                    return onError(null)
                            })
                            .catch(onError)

                    })
                    .catch(onError)
            } else {
                vm.loading = false
                vm.logout()
                return false
            }
        }


    }
})();

