(function() {
    'use strict';

    angular
        .module('BlurAdmin.pages.auth')
        .factory('authInterceptor', authInterceptor);

    authInterceptor.$inject = ['$q', '$location', '$window'];
    function authInterceptor($q, $location, $window) {
      return {
          request: function (config) {
              config.headers = config.headers || {};
              if ($window.localStorage.getItem('token')) {
                  config.headers.Authorization = 'Token ' + $window.localStorage.getItem('token');
              }
              return config || $q.when(config);
          },
          responseError: function (response) {
              if (response.status === 401 || response.status === 403) {
                  $window.localStorage.removeItem('token');
                  $location.path('/login');
              }
              return $q.reject(response);
          }
      };
    }
})();

(function () {
    'use strict';

    angular.module('BlurAdmin.pages.auth')
        .factory('AuthService', AuthService);

    /** @ngInject */
    function AuthService($http) {
        var me = function() {
            return $http.get('/api/perfiles/me/').then(function(response) { return response.data; });
        };
        //  LOGIN RECIBE COMO PARAMETRO LAS CREDENCIALES, CORREO Y SU PASSWORD
        var login = function(credentials) {
            return $http.post('/api/login/', credentials).then(function(response) { return response.data; });
        };

        var ADMIN = "100"
        var MEDICINA = "40"
        var PSICOLOGIA = "30"
        var SECRETARIA = "20"

        var hasPermission = function(user, rol) {
            var value = _.indexOf(user.roles, rol)
            if (value !== -1)
                return true
            return false
        }

        return {
            ADMIN: ADMIN,
            MEDICINA: MEDICINA,
            PSICOLOGIA: PSICOLOGIA,
            SECRETARIA: SECRETARIA,
            hasPermission: hasPermission,

            me: me,
            login: login,
        };

    }
})();

(function () {
    'use strict';

    angular.module('BlurAdmin.pages.caja')
        .controller('CerrarCajaController', CerrarCajaController);

    /** @ngInject */
    function CerrarCajaController(
        $scope,
        $rootScope,
        $window,
        $state,
        AuthService,
        UtilsService,
        AperturaCierreService,
        item,
        ingresos_egresos
    ) {
        var vm = this;

        activate();
        function activate() {
            vm.apertura_cierre_caja = item
            vm.ingresos_egresos = ingresos_egresos

            vm.ESTADOS_CAJA = UtilsService.getEstadosCaja()

            vm.total_cierre_form = ""
        }

        function _validate() {
            var title = "Error"
            var msg = "Campos incompletos. Por favor verifica."
            var error = false

            if (vm.total_cierre_form == null || vm.total_cierre_form == undefined) {
                title = "Sin total de cierre"
                msg = "Por favor, escribe el total del cierre de caja"
                error = true
            }
            else if (vm.total_cierre_form != vm.apertura_cierre_caja.total_actual && !vm.apertura_cierre_caja.notas) {
                title = "Incoherencia encontrada"
                msg = "Escriba una observacin en el apartado de notas para especificar porqu no coincide"
                        + " el efectivo en sistema y el efectivo real en caja."
                error = true
            }

            if (error)
                swal(title, msg, "error")
            return !error
        }

        function _onError(error) {
            var msg = "Ocurri un error al realizar la peticin. Por favor intenta ms tarde."
            if (error.data.detail) {
                msg = error.data.detail.toString()
            }

            return swal("Error!", msg, "error")
        }
        function _onSave(response) {
            AuthService
                .me()
                .then(function(response) {
                    $rootScope.user.caja_activa = response.caja_activa
                    $rootScope.user.id_apertura_caja = response.id_apertura_caja

                    swal("xito!", "La caja se cerr exitosamente.", "success")
                    $scope.confirm(response)
                    return $state.go('caja')
                })
                .catch(function(err){ })
        }
        vm.save = function() {
            if (_validate()) {
                swal({
                    title: "Cerrar caja?",
                    text: "Se cerrar la caja actual.",
                    type: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#e74c3c",
                    confirmButtonText: "S, cerrar caja!",
                    cancelButtonText: "No, an no",
                    closeOnConfirm: false,
                    showLoaderOnConfirm: true
                }, function() {
                    /////////////////////////////////////////////////
                    //  FIX DE TECLA TABULADOR
                    /////////////////////////////////////////////////
                    window.onkeydown = null
                    window.onfocus = null
                    /////////////////////////////////////////////////

                    var json = {
                        id: vm.apertura_cierre_caja.id,
                        estado: vm.ESTADOS_CAJA[1].id,
                        total_cierre: vm.total_cierre_form,
                        fecha_cierre: new Date(),
                        notas: vm.apertura_cierre_caja.notas
                    }

                    return AperturaCierreService.update(json, _onSave, _onError)

                })
            }
        }

    }
})();

