                        direccion: vm.perfil.persona.direccion || "",
                        foto: vm.perfil.persona.foto,

                        //  INFORMACIN DEL PERFIL
                        roles: JSON.stringify(roles),
                        profesion: profesion,
                        colegiado: vm.perfil.colegiado || ""
